<template>
  <a class="button" :class="typeClass">
    <Icon class="icon" :icon="icon" v-if="icon" />
    <div class="text" v-if="text" :class="textClass">{{ text }}</div>
  </a>
</template>

<script>
import { Icon } from "@iconify/vue";

export default {
  components: { Icon },
  props: ["text", "icon", "type"],
  computed: {
    typeClass() {
      return "type-" + this.type;
    },
    textClass() {
      if (this.text && !this.icon) {
        return "textOnly";
      }
      return null;
    },
  },
};
</script>

<style lang="scss" scoped>
@import "@/styles";

.button {
  height: 38px;
  font-size: 14px;
  border-radius: 10px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  cursor: pointer;

  filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.33));

  &:hover {
    filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.1));
  }

  .icon {
    font-size: 20px;
    width: 38px;
    color: white;
  }
}

.textOnly {
  padding: 10px 18px;
}

.type-primary {
  background-color: $colorAccent;

  &:hover {
    background-color: $colorAccent-light;
  }
}

.type-secondary {
  background-color: $colorBlack;

  &:hover {
    background-color: $colorBlack-light;
  }
}
</style>
